import Vue from 'vue'
import VueRouter from 'vue-router'
import Wsuse from '@/views/Wsuse.vue'
import Login from '@/views/Login.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'relogin',
    component: Login,
    redirect: '/login'
  },
  {
    path: '/wsuse',
    name: 'Wsuse',
    component: Wsuse
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  }
]

const router = new VueRouter({
  routes
})

// 添加全局前置导航守卫
router.beforeEach((to, from, next) => {
  // 检查用户是否已登录，如果未登录则跳转到登录页面
  const username = localStorage.getItem('username')
  const region = localStorage.getItem('region')

  if (!username && !region) {
    if (to.name !== 'Login') {
      next({ name: 'Login' })
    } else {
      next()
    }
  } else {
    next() // 继续导航
  }
})

export default router
