<template>
  <div class="chat-room">
    <el-card class="box-card">
      <div class="topTitle">
        <h1 v-if="onlineUserCount > 1">下午茶猫猫屋({{ onlineUserCount }})</h1>
        <h1 v-else>下午茶猫猫屋(1)</h1>
        <!-- <div>{{ message }}</div> -->
      </div>
      <div class="midMain">
        <ul>
          <li v-for="item in msgList" :key="item.id">
            <div>
              <p class="timeP">{{ item.sendTime }}</p>
              {{ item.user }}:<span class="msgSpan">{{ item.msg }}</span>
              <p class="regionP">IP:{{ item.region }}</p>
            </div>
          </li>
        </ul>
      </div>
      <div class="bottomBg">
        <div class="bottom-box">
          <el-input v-model="nMsg" placeholder="请输入内容" style="width: 350px" @keyup.native.enter="sendMessage"></el-input>
          <el-button @click="sendMessage" round>发送</el-button>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      message: '你好',
      nMsg: '',
      msgList: [],
      ws: null
      // numberOfOnlineUser: null
    }
  },
  mounted() {
    this.initializeWebSocket()
  },
  computed: {
    onlineUserCount() {
      const userSet = new Set()
      this.msgList.forEach(item => {
        userSet.add(item.user)
      })
      return userSet.size
    }
  },
  methods: {
    // 初始化WebSocket
    initializeWebSocket() {
      this.ws = new WebSocket('ws://127.0.0.1:8000')

      // 连接建立时触发
      this.ws.onopen = () => {
        console.log('WebSocket连接已打开')
        // 使用连接发送数据
        this.ws.send('success')
      }

      // 客户端接收服务端数据时触发
      this.ws.onmessage = message => {
        this.message = message.data
        if (this.message !== 'success') {
          const f1 = JSON.parse(this.message)
          const f2 = JSON.parse(f1.content)
          // console.log(typeof f2)
          this.msgList.push(f2)
          // this.numberOfOnlineUser = f1.clientsCount
          // console.log(this.numberOfOnlineUser)
          // console.log(this.msgList)
        }

        // console.log('获取WebSocket消息:', this.message)
      }

      // 	通信发生错误时触发
      this.ws.onerror = () => {
        console.error('WebSocket连接出错')
      }
    },
    sendMessage() {
      if (this.nMsg === '') {
        return
      }
      const nMessage = this.nMsg
      const username = localStorage.getItem('username')
      const region = localStorage.getItem('region')
      const getTime = this.getFormattedTime()
      if (nMessage !== '') {
        this.ws.send(
          JSON.stringify({
            id: Math.floor(Math.random() * 10000),
            user: username,
            sendTime: getTime,
            msg: this.nMsg,
            region: region
          })
        )
        this.nMsg = ''
      }
    },
    getFormattedTime() {
      const now = new Date()

      // 获取小时、分钟、秒数
      const hours = String(now.getHours()).padStart(2, '0')
      const minutes = String(now.getMinutes()).padStart(2, '0')
      const seconds = String(now.getSeconds()).padStart(2, '0')

      // 拼接时间字符串
      const formattedTime = `${hours}:${minutes}:${seconds}`

      return formattedTime
    },
    textenter() {
      console.log(123)
    }
  }
}
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

.chat-room {
  width: 100vw;
  height: 100vh;
  background: url('@/assets/img/textura.png');
  background-color: #2d5ffd;
  display: flex;
}

.box-card {
  position: relative;
  margin: auto;
  height: 650px;
  width: 480px;
}

.topTitle {
  text-align: center;
}

.midMain {
  margin: 0;
  padding: 20px;
  overflow-y: scroll;
  height: 500px;
}

/* 隐藏默认的浏览器滚动条 */
.midMain::-webkit-scrollbar {
  width: 8px;
}

/* 背景色 */
.midMain::-webkit-scrollbar-track {
  background-color: #f1f1f1;
}

/* 滑块 */
.midMain::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 4px;
}

/* 鼠标悬停时滑块 */
.midMain::-webkit-scrollbar-thumb:hover {
  background-color: rgba(0, 0, 0, 0.5);
}

.timeP {
  font-size: 12px;
  color: #666666;
}

.regionP {
  font-size: 12px;
  color: #666666;
  text-align: right;
}

.msgSpan {
  border: 1px solid;
  border-radius: 6px;
  padding: 2px;
  background-color: #12b7f5;
  line-height: 30px;
}

.el-card >>> .el-card__body {
  padding: 0 !important;
  /* background: green; */
}

.bottomBg {
  width: 100%;
  height: 65px;
  position: absolute;
  bottom: 0;
  background: #e8e8e8;
}

.bottom-box {
  position: relative;
  width: 100%;
  height: 100%;
}

.bottom-box .el-button {
  position: absolute;
  right: 15px;
  bottom: 13px;
}
.bottom-box .el-input {
  position: absolute;
  left: 15px;
  bottom: 13px;
}
</style>
