<template>
  <div class="login">
    <el-card class="box-card">
      <div class="topTitle">
        <div class="Ltitle"><h2>猫猫茶馆</h2></div>
      </div>
      <div class="midMain">
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="用户名" class="formitem-hightline">
            <el-input class="el-input-width" v-model="form.username"></el-input>
          </el-form-item>
          <el-form-item label="用户IP" class="formitem-hightline">
            <el-input class="el-input-width" v-model="form.region" disabled></el-input>
            <el-button class="get-region-btn" @click="getProvince" round>获取定位</el-button>
          </el-form-item>
          <el-form-item class="enterRoom formitem-hightline">
            <el-button type="primary" @click="onSubmit" round>进入</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="test">
        <div></div>
      </div>
    </el-card>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data() {
    return {
      form: {
        username: '',
        region: ''
      }
    }
  },
  methods: {
    onSubmit() {
      if (this.form.username === '') {
        return alert('请填写用户名')
      }
      if (this.form.region === '') {
        return alert('请使用定位')
      }
      localStorage.setItem('username', this.form.username)
      localStorage.setItem('region', this.form.region)
      this.$router.push('/wsuse')
    },
    getProvince() {
      axios
        .get('https://api.ipify.org?format=json')
        .then(response => {
          const ipAddress = response.data.ip
          axios
            .get(`https://ipapi.co/${ipAddress}/json/`)
            .then(response => {
              this.form.region = response.data.region
            })
            .catch(error => {
              console.log(error)
            })
        })
        .catch(error => {
          console.log(error)
        })
    }
  }
}
</script>

<style scoped>
