  margin: 0;
  padding: 0;
}

:global(body) {
  margin: 0;
  padding: 0;
}

.login {
  width: 100vw;
  height: 100vh;
  background: url('@/assets/img/preview.jpg') no-repeat;
  background-size: cover;
  /* background-color: #2d5ffd; */
  display: flex;
}

.box-card {
  width: 480px;
  height: 370px;
  margin: auto;
  background-color: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(5px);
}
/*
