}
*/

.text {
  font-size: 14px;
}

.item {
  padding: 18px 0;
}

.Ltitle {
  width: 340px;
  height: 72px;
  background: url('@/assets/img/title.png') no-repeat;
  background-size: cover;
  margin: 0 auto;
}

.Ltitle h2 {
  text-align: center;
  line-height: 70px;
  color: #f8bf00;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.midMain {
  margin: 30px 0;
}

